<template>
	<div>
		<HeaderComponent :view-title="activityTitle"></HeaderComponent>
		<nav class="activity-nav">
			<router-link class="activated" to="/activity/baseSetting">基本设置</router-link>
			<svg class="iconActivity" aria-hidden="true">
	  			<use xlink:href="#icon-jiantouyou"></use>
			</svg>
			<router-link to="/activity/awardSetting">奖品设置</router-link>
			<svg class="iconActivity" aria-hidden="true">
	  			<use xlink:href="#icon-jiantouyou"></use>
			</svg> 
			<router-link to="/activity/otherSetting">其他设置</router-link>
			<svg class="iconActivity" aria-hidden="true">
	  			<use xlink:href="#icon-jiantouyou"></use>
			</svg> 
			<router-link to="/activity/seniorSetting">高级设置</router-link> 
		</nav>
		<div id="activity-main">
			<router-view></router-view>
		</div>
	</div>
</template>

<script type="text/javascript">
	import HeaderComponent from '../../components/header/header';
	export default {
		activityTitle:Object,
		data(){
			return{
				activityTitle:{
					name:'创建活动',
					showCode:1
				}
			}
		},
		mounted(){},
		components:{
			HeaderComponent
		}
	}
</script>
<style lang="scss" scoped>
	@import '../../style/mixin';
	.iconActivity {
	   width: 0.7rem; height: 0.7rem;
	   vertical-align: -0.15em;
	   fill: currentColor;
	   overflow: hidden;
	   color:#929292;
	}
	.activity-nav{
		height:1.5rem;
		line-height: 1.2rem;
		padding:0 0.4rem;
		background:#fff;
		margin-top:2.2rem;
		a{
			@include sc(0.7rem,#929292);
		}
		svg{
			vertical-align: middle;
		}
	}
	.activated{
		color:#333 !important;
	}
</style>


